import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'main',
      component: resolve => require(['@/views/main.vue'], resolve),
      redirect: '/home',
      children: [
        {
          path: '/home',
          name: 'home',
          component: resolve => require(['@/views/home.vue'], resolve),
        },
        {
          path: '/contact',
          name: 'contact',
          component: resolve => require(['@/views/contact.vue'], resolve),
        },
      ],
    },
    {
      path: '/article',
      name: 'article',
      component: resolve => require(['@/views/article.vue'], resolve),
    },
    {
      path: '/product-list',
      name: 'product-list',
      component: resolve => require(['@/views/product-list.vue'], resolve),
    },
    {
      path: '/product',
      name: 'product',
      component: resolve => require(['@/views/product.vue'], resolve),
    },
    {
      path: '/article-add',
      name: 'article-add',
      component: resolve => require(['@/views/article-add.vue'], resolve),
    },
  ],
});



